import produce from "immer";
import { AppObject, ObjectTypes } from "../../editorObjects/Editor";
import { AppEventType, RemoveObjects, UngroupObjects } from "../AppEventType";
import { BaseEventHandler } from "../BaseHandlers/BaseEventHandler";
import { setStore } from "../store";
import { RemoveObjectsHistory, UngroupObjectsHistory } from "../AppHistory";
import {
  addObjectByObjectType,
  getTopBarPosition,
  triggerDeleteObjectIds,
  triggerSelectBead,
  triggerSelectRegion,
} from "../../threejs/eventEmitterHandlers";
import { refreshReactState } from "../refreshReactState";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import dayjs from "dayjs";
import { cloneDeep } from "lodash";

export class UngroupObjectsHandler extends StoreObjectHandler<UngroupObjects> {
  run() {
    // const { objects } = this.event.payload;

    const groupIds: string[] = [];
    const idsInGroup: string[] = [];
    const idsWithoutGroup: string[] = [];
    const removed: AppObject[] = [];
    const historyObjects = this.event.payload.objects.map((obj) =>
      cloneDeep(obj)
    );
    let newStore = produce(this.store, (draft) => {
      let groupCount: any = 0;
      let inGroupCount: any = 0;
      let noGroupCount: any = 0;
      this.event.payload.objects.forEach((obj) => {
        const isGroup = obj.objectType === ObjectTypes.Group;
        const isInGroup = obj.groupId !== undefined && obj.groupId!.length > 0;
        console.log("co w object?", obj.id, obj.objectType, obj.groupId);
        if (isGroup) {
          groupCount = groupCount + 1;
          groupIds.push(obj.id!);
        } else if (isInGroup) {
          inGroupCount = inGroupCount + 1;
          idsWithoutGroup.push(obj.id!);
          idsInGroup.push(obj.id!);
        } else {
          noGroupCount = noGroupCount + 1;
          idsWithoutGroup.push(obj.id!);
        }
      });

      console.log("czy jest grupa wyznaczona?", groupIds);
      groupIds.forEach((id) => {
        if (draft.objects[id] === undefined) {
          return;
        }
        this.selectObjectOnThreeScene(draft.objects[id], false);
        removed.push(cloneDeep(draft.objects[id]));
        delete draft.objects[id];
      });

      idsInGroup.forEach((id) => {
        draft.objects[id].groupId = undefined;
      });

      draft.selectedIds = [...idsWithoutGroup];
    });

    if (this.saveToHistory) {
      const historyEvent: UngroupObjectsHistory = {
        do: this.event,
        // @TODO: co musi byc  wewnatrz? Trzeba podmieci ID grupy ktora pojawia sie
        redo: {
          name: AppEventType.UngroupObjects,
          payload: {
            objects: newStore.selectedIds.map((id) => {
              return cloneDeep(newStore.objects[id]);
            }),
          },
        },
        undo: {
          name: AppEventType.GroupObjects,
          payload: {
            objects: historyObjects,
          },
        },
        createdAt: dayjs().toISOString(),
      };
      this.history.push(historyEvent);
    }
    newStore = this.getStoreWithHistory(newStore);

    this.event.payload.objects.forEach((object) => {
      this.selectObjectOnThreeScene(object as any, false);
    });

    console.log("czy jest usuniety?", removed);

    triggerDeleteObjectIds(
      removed.map((obj) => {
        return { id: obj.id!, objectType: obj.objectType! };
      })
    );

    triggerDeleteObjectIds(
      this.event.payload.objects.map((obj) => {
        return { id: obj.id!, objectType: obj.objectType! };
      })
    );

    idsWithoutGroup.forEach((id) => {
      addObjectByObjectType(newStore.objects[id]);
    });

    newStore.selectedIds.forEach((id) => {
      this.selectObjectOnThreeScene(newStore.objects[id], true);
    });

    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);
  }
}
